import { FC, useCallback } from 'react';
import { useSelector } from 'react-redux';

import Head from 'next/head';

import { Icon } from '@openfin/ui-library';

import { showSuccess } from '@common/utils/indicators/showIndicator';
import { getUrlParams, Router, withRouter } from '@common/utils/router';

import { useTranslation } from '@common/api/i18next';
import { getTabsHandler } from '@common/api/tabs';

import { selectActivePage } from '@enterprise/slices/pages/attached';

import Callout from '@enterprise/components/Callout/Callout';
import PopupButton from '@enterprise/components/ControlBar/BaseButton/PopupButton';

interface Props {
    router: Router;
}

const LayoutsPopup: FC<Props> = ({ router }): JSX.Element => {
    const { t } = useTranslation();
    const activePage = useSelector(selectActivePage);

    const applyLayout = useCallback(
        async (layoutName: string, preset: any) => {
            const parentName = getUrlParams(router.asPath).parentName as string;
            const parentIdentity = { uuid: fin.me.uuid, name: parentName };
            const parentBrowserLayout = fin.Platform.Layout.wrapSync(parentIdentity);

            const tabsHandler = getTabsHandler({ ...parentIdentity, layoutName: activePage.layoutContainerKey });
            if (preset === 'tabs' && !(await tabsHandler.isShowingTabs())) {
                await tabsHandler.show();
            }

            // Display an info indicator that the new layout has been applied
            showSuccess({
                message: t('successIndicator.layoutApplied', { layoutName }),
                parentBrowserName: parentIdentity.name
            });
            // Apply the new layout
            await parentBrowserLayout.applyPreset({ presetType: preset });
        },
        [activePage]
    );

    return (
        <>
            <Head>
                <title>Browser Layouts Popup</title>
            </Head>
            <Callout aria-label={t('aria.escapeLayoutDialog')}>
                <PopupButton title={t('changeLayout.fullWidth')} onClick={() => applyLayout('Full width', 'tabs')}>
                    <Icon icon="SquareIcon" />
                </PopupButton>
                <PopupButton title={t('changeLayout.columns')} onClick={() => applyLayout('Column', 'columns')}>
                    <Icon icon="ViewVerticalIcon" />
                </PopupButton>
                <PopupButton title={t('changeLayout.Rows')} onClick={() => applyLayout('Row', 'rows')}>
                    <Icon icon="ViewHorizontalIcon" />
                </PopupButton>
                <PopupButton title={t('changeLayout.Grid')} onClick={() => applyLayout('Grid', 'grid')}>
                    <Icon icon="GridIcon" />
                </PopupButton>
            </Callout>
        </>
    );
};

export default withRouter(LayoutsPopup);
